.about {
    margin: 40px auto; /* Center the container */
    padding: 30px;
    background: #fff;
    border-radius: 15px;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
    display: flex;
    align-items: center;
    gap: 30px;
    max-width: 1200px; /* Limit maximum width for better readability */
    border: 5px solid #007bff; /* Beautiful border */
    overflow: hidden;
    margin-left: 180px;
}

.about:hover {
    transform: translateY(-5px);
    box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.2);
}

.about-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.about-img {
    flex: 1;
    max-width: 50%;
    border-right: 5px solid #007bff; /* Border between image and content */
    padding-right: 20px;
}

.about-img img {
    width: 100%;
    height: 400px;
    object-fit: cover;
    border-radius: 10px;
    transition: transform 0.3s ease-in-out;
}

.about-img img:hover {
    transform: scale(1.05);
}

.about-content {
    flex: 1;
    padding: 20px;
    text-align: left;
}

.about-content h1 {
    color: #333;
    font-family: 'Poppins', sans-serif;
    border-bottom: 3px solid #007bff; /* Stylish border */
    padding-bottom: 10px;
    font-size: 28px;
    font-weight: 600;
}

.about-content p {
    font-size: 16px;
    line-height: 1.8;
    color: #555;
}

/* Responsive adjustments */
@media (max-width: 1024px) {
    .about {
        flex-direction: column;
        padding: 20px;
    }

    .about-img {
        max-width: 100%;
        border-right: none;
        border-bottom: 5px solid #007bff;
        padding-right: 0;
        padding-bottom: 20px;
    }

    .about-content {
        max-width: 100%;
        text-align: center;
    }
}

@media (max-width: 768px) {
    .about-img img {
        height: 250px;
    }

    .about-content h1 {
        font-size: 24px;
    }

    .about-content p {
        font-size: 14px;
    }
}

@media (max-width: 480px) {
    .about {
        margin: 10px;
        padding: 10px;
    }

    .about-img img {
        height: 200px;
    }

    .about-content h1 {
        font-size: 20px;
    }

    .about-content p {
        font-size: 13px;
    }
}
