/* âœ… Sidebar Styling - Consistent in Both Modes */
.sidebar-section {
  display: flex;
  background-color: black;
}

.sidebar {
  height: 100vh;
  background-color: #1e1e2c;
  width: 90px;
  position: fixed;
}

/* Sidebar Toggle Button */
.sidebar-toggle {
  width: 200px;
}

.sidebar-toggle-icons {
  display: flex;
  align-items: right;
  justify-content: right;
  cursor: pointer;
}

/* Sidebar Icons - Fixed Styling */
.sidebar-toggle-icons p svg {
  background-color: #f25967;
  color: white;
  margin-top: 30px;
}

/* ðŸ”§ Fix Sidebar in Dark Mode */
.dark .sidebar-section {
  background-color: black;
}

.dark .sidebar {
  background-color: #1e1e2c;
}

.dark .sidebar-toggle-icons p svg {
  background-color: #f25967;
  color: white;
}