/* Experience Section */
.experience-container {
  max-width: 900px;
  margin: 50px auto;
  text-align: center;
  padding: 20px;
  transform: translateX(100px); /* Move 100px to the right */
}

.experience-title {
  font-size: 2rem;
  text-transform: uppercase;
  margin-bottom: 30px;
  color: #2C3E50;
}

/* Experience Grid */
.experience-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: 20px;
}

/* Experience Card */
.experience-card {
  background: #ffffff;
  border-radius: 12px;
  padding: 20px;
  text-align: left;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease-in-out;
  border-left: 6px solid #3498db;
  position: relative;
}

.experience-card:hover {
  transform: translateY(-5px);
}

/* Experience Header */
.experience-header {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 10px;
}

.experience-icon {
  font-size: 26px;
  color: #3498db;
}

.experience-position {
  font-size: 1.4rem;
  font-weight: bold;
  color: #2C3E50;
}

/* Experience Text */
.experience-company {
  font-size: 1.2rem;
  font-weight: bold;
  color: #333;
}

.experience-year {
  font-size: 1rem;
  font-weight: bold;
  color: #3498db;
}

.experience-description {
  font-size: 0.95rem;
  color: #666;
  margin-top: 8px;
  line-height: 1.5;
}
