/* Project Section */
.project {
    margin: 80px 0;
    margin-left: 140px;
    text-align: center;
}

.project-title {
    font-size: 32px;
    font-weight: bold;
    color: #138781;
    margin-bottom: 20px;
}

/* Project Grid */
.project-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    gap: 30px;
    justify-content: center;
    padding: 20px;
}

/* Project Card */
.project-card {
    background: #ffffff;
    border-radius: 12px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.15);
}

/* Project Image */
.project-card img {
    width: 100%;
    height: 180px;
    object-fit: cover;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
}

/* Project Content */
.project-card h3 {
    font-size: 20px;
    color: #333;
    margin: 15px 0;
}

.project-card p {
    font-size: 14px;
    color: #666;
    padding: 0 15px;
    line-height: 1.5;
}

/* Project Tags */
.project-tags {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 10px;
    gap: 10px;
}

.tag {
    background-color: #e0b50f;
    color: black;
    font-size: 12px;
    font-weight: bold;
    padding: 5px 12px;
    border-radius: 20px;
    text-transform: uppercase;
}

/* Responsive Design */
@media (max-width: 768px) {
    .project {
        margin-left: 20px;
        margin-right: 20px;
    }

    .project-grid {
        grid-template-columns: 1fr;
    }
}
