/* Profile Picture */
.navbar-profile-pic {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 15px;
  }
  
  .navbar-profile-pic img {
    width: 120px;
    height: 120px;
    border-radius: 50%;
    border: 3px solid #f25967;
    box-shadow: 0px 0px 10px rgba(242, 89, 103, 0.5);
    object-fit: cover;
    transition: transform 0.3s ease-in-out;
  }
  
  .navbar-profile-pic img:hover {
    transform: scale(1.1);
  }
  
  /* Navigation Container */
  .nav-items {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 0;
  }
  
  /* Navigation Links */
  .nav-item {
    display: flex;
    flex-direction: column;
    gap: 20px;
    width: 100%;
  }
  
  .nav-link {
    display: flex;
    align-items: center;
    gap: 12px;
    font-size: 18px;
    font-weight: 500;
    color: white;
    text-decoration: none;
    padding: 10px 15px;
    border-radius: 8px;
    transition: all 0.3s ease-in-out;
    width: 90%;
  }
  
  .nav-link svg {
    font-size: 22px;
  }
  
  /* Active & Hover Effects */
  .nav-link:hover,
  .nav-link.active {
    background-color: rgba(242, 89, 103, 0.2);
    color: #f25967;
    transform: translateX(5px);
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .navbar-profile-pic img {
      width: 100px;
      height: 100px;
    }
  
    .nav-link {
      font-size: 16px;
      padding: 8px 12px;
    }
  
    .nav-link svg {
      font-size: 20px;
    }
  }