/* ðŸŒŸ Education Section */
.education-section {
    width: 100%;
    text-align: center;
    padding: 80px 20px;
    background: linear-gradient(to right, #0f2027, #203a43, #2c5364);
    color: white;
    margin-left: 100px;
  }
  
  .education-title {
    font-size: 3rem;
    font-weight: bold;
    text-transform: uppercase;
    margin-bottom: 50px;
    letter-spacing: 2px;
    position: relative;
    display: inline-block;
  }
  
  .education-title::after {
    content: "";
    display: block;
    width: 60%;
    height: 4px;
    background: #ffcc00;
    margin: 12px auto 0;
    border-radius: 2px;
  }
  
  /* ðŸ“Œ Education Container */
  .education-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 25px;
  }
  
  /* ðŸ“Œ Education Card */
  .education-card {
    width: 340px;
    background: rgba(255, 255, 255, 0.15);
    
    border-radius: 14px;
    padding: 25px;
    box-shadow: 0px 8px 20px rgba(255, 255, 255, 0.2);
    text-align: center;
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
    border: 1px solid rgba(255, 255, 255, 0.3);
    cursor: pointer;
  }
  
  /* ðŸŸ¡ Hover Effect */
  .education-card:hover {
    transform: scale(1.05);
    box-shadow: 0px 12px 25px rgba(255, 204, 0, 0.8);
  }
  
  /* ðŸ“Œ Icon */
  .education-icon {
    font-size: 42px;
    color: #ffcc00;
  }
  
  /* ðŸ“Œ Card Body */
  .card-body h3 {
    font-size: 1.6rem;
    font-weight: bold;
    color: #ffcc00;
    margin-bottom: 6px;
  }
  
  .card-body h4 {
    font-size: 1.3rem;
    color: #ffffff;
    opacity: 0.9;
    margin-bottom: 6px;
  }
  
  .card-body p {
    font-size: 1.1rem;
    color: #ddd;
    font-style: italic;
  }
  
  /* ðŸ“± Responsive Design */
  @media (max-width: 768px) {
    .education-container {
      flex-direction: column;
      align-items: center;
    }
  
    .education-title {
      font-size: 2.5rem;
    }
  }
  