/* Home Section */
.home {
  height: 300px;
  color: white;
  display: flex;
  align-items: center;
  text-align: left;
  padding: 20px;
}

.home-content {
  padding: 10px;
  padding-top: 80px;
  margin-left: 100px; /* Moves the main content to the right */
}

/* Keeps the Typewriter text aligned to the left */
.home-content h1 {
  padding: 5px;
  color: #e0b50f;
  margin-left: 0; /* Ensures it's aligned to the left */
}

/* Buttons Section */
.home-buttons {
  margin-left: 100px; /* Keeps buttons aligned with the content */
  margin-top: 20px;
  display: flex;
  gap: 15px;
}

.home-buttons .btn {
  padding: 10px 15px;
  border: none;
  font-size: 1rem;
  font-weight: bold;
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
  border-radius: 5px;
  transition: all 0.3s ease-in-out;
}

/* Hire Me Button */
.home-buttons .btn-hire {
  background-color: #3b8ff3;
  color: white;
  width: 160px;
}

.home-buttons .btn-hire:hover {
  background-color: white;
  color: #3b8ff3;
  width: 180px;
}

/* Download CV Button */
.home-buttons .btn-cv {
  background-color: #f29f67;
  color: white;
  width: 180px;
}

.home-buttons .btn-cv:hover {
  background-color: white;
  color: #f29f67;
  width: 200px;
}

/* Theme Toggle Button */
.theme-toggle {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 50px;
  background-color: #1e1e1e;
  margin-top: 20px;
  text-align: center;
  position: fixed;
  right: 0;
  cursor: pointer;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  z-index: 100;
  transition: background 0.3s ease-in-out;
}

.theme-toggle:hover {
  background-color: #333;
}
